import React, { Component } from 'react';
import FacebookProvider, { Like, Page, CommentsCount, SendToMessenger, CustomChat, MessageUs, MessengerCheckbox } from 'react-facebook';

import '../styles/aboutus.css';

class AboutUs extends Component {
  render() {
    return (
      <div className="container">
        <div className="aboutus">
        <FacebookProvider appId="234779533734920">
        <MessageUs appId="234779533734920" pageId="855248294542133"/>
        </FacebookProvider>

        <FacebookProvider appId="234779533734920">
        <MessengerCheckbox appId="234779533734920" pageId="855248294542133"></MessengerCheckbox>
        <SendToMessenger appId="234779533734920" pageId="855248294542133"> </SendToMessenger>
        <CustomChat appId="234779533734920" pageId="855248294542133"></CustomChat>
        </FacebookProvider>
        </div>
      </div>
    );
  };
}
export default AboutUs;



